window.addEventListener('DOMContentLoaded', () =>{

    // табы
    const tabs = document.querySelectorAll('.tabheader__item');
    const tabsContent = document.querySelectorAll('.tabcontent');
    const tabsParent = document.querySelector('.tabheader__items');

    function hideTabContent() {
        tabsContent.forEach((item) => {
            item.classList.add('hide');
            item.classList.remove('show', 'fade');

        });
        tabs.forEach((item) => {
            item.classList.remove('tabheader__item_active')
        });
    }

    function showTabContent(i = 0) { // 0 по умолчанию
        tabsContent[i].classList.remove('hide');
        tabsContent[i].classList.add('show', 'fade');
        tabs[i].classList.add('tabheader__item_active')
    }
    hideTabContent()
    showTabContent()

    tabsParent.addEventListener('click', (event) => {
       if (event.target && event.target.classList.contains('tabheader__item')) {
        tabs.forEach((item, i) => {
            if (item == event.target) {
                hideTabContent()
                showTabContent(i)
            }
        })
       } 
    })

    // таймер обратного отсчета
    const deadLine = '2023-05-11'
    function getTimeRemaining(endtime) {
        let days, hours, minutes, seconds; // для того чтобы вставить 0 если отрицательная t
        const t = Date.parse(endtime) - Date.parse(new Date()); // кол-во миллисекунд от разницы конечной даты и сегодняшней

        if (t <= 0) {
            days = 0;
            hours = 0;
            minutes = 0;
            seconds = 0;
        } else {
        days = Math.floor(t / (1000 * 60 * 60 * 24)); // миллисекунд в дне 
        hours = Math.floor((t / (1000 * 60 * 60) % 24)); // делит и возвращает остаток (часы)
        minutes = Math.floor((t / 1000 / 60 ) % 60); // делит и возращает остаток (минуты)
        seconds = Math.floor((t / 1000 ) % 60); // делит и возращает остаток (секунды)
        }
        

        return{
            'total' : t,
            'days' : days,
            'hours' : hours,
            'minutes' : minutes,
            'seconds' : seconds
        };
    }


    function getZero(num) { // добавляем 0 если число меньше 10
        if (num >= 0 && num < 10) {
            return `0${num}`
        } else{
            return num
        }
    }

    function setClock(selector, endtime) {
        
        const timer = document.querySelector(selector);
        const days = timer.querySelector('#days');
        const hours = timer.querySelector('#hours');
        const minutes = timer.querySelector('#minutes');
        const seconds = timer.querySelector('#seconds');
        const timeInterval = setInterval(updateClock, 1000)

        updateClock();

            function updateClock() {
                const t = getTimeRemaining(endtime)

                days.innerHTML = getZero(t.days);
                hours.innerHTML = getZero(t.hours);
                minutes.innerHTML = getZero(t.minutes);
                seconds.innerHTML = getZero(t.seconds);

                if (t.total <= 0) {
                    clearInterval(timeInterval)
                }
            }
    }


    setClock('.timer', deadLine)

    // модальное окно

    const modalTrigger = document.querySelectorAll('[data-modal]');
    const modal = document.querySelector('.modal');
    const modalCloseBtn = document.querySelector('[data-close]');

    function openModal() {
        //можно использовать toggle
        modal.classList.remove('hide');
        modal.classList.add('show');
        document.body.style.overflow = 'hidden'
        //clearInterval(modalTimerId) // не показывать модалку если пользователь уже ее открыл
    }

    modalTrigger.forEach((btn) => {
        btn.addEventListener('click', openModal);
    });



    function closeModal() {
        
        if (window.pageYOffset + document.documentElement.clientHeight >= document.
            documentElement.scrollHeight - 1 ){
               window.scrollTo(0, scrollY - 2)
                console.log('hi')
            }
        modal.classList.add('hide');
        modal.classList.remove('show');
        document.body.style.overflow = '';
    }

    modalCloseBtn.addEventListener('click', closeModal);

    modal.addEventListener('click', (e) => {
        if (e.target === modal) {
            closeModal() 
        }
    })

    document.addEventListener('keydown', (e) => {
        if (e.code === 'Escape' && modal.classList.contains('show')) {
            closeModal() 
        }
    });

    //const modalTimerId = setTimeout(openModal, 5000) // показать модалку через 5сек

function showModalByScroll() {
    if (window.scrollY + document.documentElement.clientHeight >= document.
        documentElement.scrollHeight - 1 ) { // прокрученная часть + видимая клиентом часть >= всей высоты документа -1 (для некоторых браузеров) | (то есть конец страницы) 
            openModal()
            window.removeEventListener('scroll', showModalByScroll) // убрать слушатель когда один раз пользователь долистае до конца
    }
}

    window.addEventListener('scroll', showModalByScroll);

    // используем классы для карточек
    class MenuCart {
        constructor (src, alt, title, descr, price, parentSelector, ...classes) {
            this.src = src;
            this.alt = alt;
            this.title = title;
            this.descr = descr;
            this.price = price;
            this.transfer = 50;
            this.classes = classes;
            this.parent = document.querySelector(parentSelector);
            this.changeToRu();
        }

        changeToRu() {
            this.price *= this.transfer;
        }

        render() {
            const element = document.createElement('div')
            if (this.classes.length === 0) { // если класса "menu__item" не существует
                this.element = 'menu__item';
                element.classList.add(this.element)
            } else {
                this.classes.forEach((className) => element.classList.add(className));
            }

            element.innerHTML = `<img src=${this.src} alt=${this.alt}>
            <h3 class="menu__item-subtitle">${this.title}</h3>
            <div class="menu__item-descr">${this.descr}</div>
            <div class="menu__item-divider"></div>
            <div class="menu__item-price">
                <div class="menu__item-cost">Цена:</div>
                <div class="menu__item-total"><span>${this.price}</span> руб/день</div>
            </div>`;
        this.parent.append(element);
        }
    }

    //const div = new MenuCart("img/tabs/vegy.jpg", 'vegy', 'Меню "Фитнес"', 'wefEWF', 30, ".menu .container");
    //div.render();
    new MenuCart(
        "img/tabs/vegy.jpg",
        "vegy",
        "Меню 'Фитнес'",
        "Меню 'Фитнес' - это новый подход к приготовлению блюд: больше свежих овощей и фруктов. Продукт активных и здоровых людей. Это абсолютно новый продукт с оптимальной ценой и высоким качеством!",
        30,
        ".menu .container",
        
    ).render();
    new MenuCart(
        "img/tabs/elite.jpg",
        "elite",
        "Меню 'Премиум'",
        "В меню 'Премиум' мы используем не только красивый дизайн упаковки, но и качественное исполнение блюд. Красная рыба, морепродукты, фрукты - ресторанное меню без похода в ресторан!",
        40,
        ".menu .container",
        "menu__item",
        "big"
    ).render();
    new MenuCart(
        "img/tabs/post.jpg",
        "post",
        "Меню 'Постное'",
        "Меню 'Постное' - это тщательный подбор ингредиентов: полное отсутствие продуктов животного происхождения, молоко из миндаля, овса, кокоса или гречки, правильное количество белков за счет тофу и импортных вегетарианских стейков.",
        40,
        ".menu .container",
        "menu__item"
    ).render();
});